<template>
  <div class="project-resume">
    <div class="name">
      {{ name }}
    </div>
    <p v-for="info in infos">
      {{ info.text }}
    </p>
    <p class="resume">
      {{ resume }}
    </p>
  </div>
</template>

<style lang="scss" scoped>
.project-resume{
  .name{
    font-weight: bold;
  }

  p{
    margin: 0;
  }

  .resume{
    text-align: justify;
    text-indent: 2em;
    margin-left: 2em;
    margin-top: 1em;
    margin-bottom: 1em;
  }
}
</style>

<script>
export default{
  name: 'project-resume',
  props: {
    name: {
      type: String,
      default: 'Project name',
    },
    infos: {
      type: Array,
      default: [],
    },
    resume: {
      type: String,
      default: '',
    },
  },
  data: function(){
    return {};
  },
};
</script>



