<template>
  <div id="app">
    <resume :resume-data="resumeData"></resume>
  </div>
</template>

<script>
import Resume from './components/Resume';
import resumeData from './resume-data';

export default {
  name: 'app',
  components: {
    'resume': Resume,
  },
  data: function(){
    return {
      resumeData: resumeData,
    };
  },
};
</script>

<style lang="scss">
@import "./components/css/media-queries-breakpoints";
@import "./components/css/utils";

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  max-width: 800px;
  padding-top: 40px;
  padding-right: 25px;
  padding-left: 25px;
  background-image: url(./assets/white-background.png);
  margin-left: auto;
  margin-right: auto;
  @include xs{
    @include break-all;
    padding-right: 0px;
    padding-left: 0px;
  }
}
body{
  margin: 0;
  background-image: url(./assets/gray-background.png);
}
</style>



